.veraxhome {
    grid-column: 1/13;
    grid-row: 2/11;
    display: grid;
    width: 100%;
    gap: 10px;
    overflow-x: hidden;
    overflow-y: auto;
    white-space: nowrap;
    position: relative;
  }

.contentSection
{
    grid-column: 9/13;
    grid-row: 2/11;
    display: grid;
    
}

:root {
    --labelDivider: rgba(0, 0, 0, 0.15);
    --web-navigation-width: 18.8843vw;

}

.contentContaienr
{
    display: flex;
    flex-direction: column;
    /* grid-area: structure-main-section; */
    height: auto;
    overflow-x: hidden;
    overflow-y: auto;
    transition: margin .1s linear,width .1s linear;
    width: 100%;
    z-index: 1;
    padding-top: 10px;

}

.album-list1, .album-list2 {
    display: flex;
    gap: 10px;
    max-width: 100%;
    white-space:nowrap;
	overflow-x:scroll; 
    

  }

  .album-list1::-webkit-scrollbar,
.album-list2::-webkit-scrollbar {
    display: none; /* Chrome, Safari, Edge 브라우저용 */
}
  
  .album-image1, .album-image2 {
    width: 12vw; /* 화면 너비의 12%로 설정 */
    height: auto; /* 가로 세로 비율 유지 */
    min-width: 144px;
    max-width: 200px; /* 최대 너비는 200px로 제한 */
   
}
 

.featured-image {
    width: 20vw; /* 화면 너비의 12%로 설정 */
    height: auto; /* 가로 세로 비율 유지 */
    min-width: 300px;
    max-width: 400px; /* 최대 너비는 200px로 제한 */
    border-radius: 10px; /* 10px만큼 둥글게 처리 */

   
}

.onlyPc{
    display: grid;
    grid-column: 1/9;
    grid-row: 2/11;
    
    border-right: 1px solid var(--labelDivider);


}

.homeNavbar {
    text-align: center;
    list-style-type: none; /* 리스트 스타일을 제거합니다. */
    padding: 0; /* 패딩을 초기화합니다. */
    height: 330px;
}



 @media screen and (max-width: 484px){
    .onlyPc{
    display: none;
    }
 }
